      //   //   { text: '已鎖定', value: 1 }
      //   // ]
      // },
      // // 每頁顯示筆數
      // selectRowsPerPageItems: {
      //   selected: 25, // (若調整該值，需更新 rowsTableHeightClass 預設值)
      //   options: [] // *initLanguage*
      //   // options: [
      //   //   { text: '5筆', value: 5 },
      //   //   { text: '10筆', value: 10 },
      //   //   { text: '25筆', value: 25 },
      //   //   { text: '50筆', value: 50 }
      //   // ]
      // },
      // // 對應 API 體育代號名稱
      // mapSportNameApi: {
